.header {
	left        : 0;
	padding-top : 12px;
	position    : absolute;
	top         : 0;
	width       : 100%;
	z-index     : 100;

	@media screen and (min-width : $break-table) {
		padding-top : 48px;
	}
	@media screen and (min-width : $break-desktop) {

	}
	@media screen and (min-width : $break-desktop-lg) {
		padding-top : 64px;
	}

	.line--top {
		top : 12px;
		@media screen and (min-width : $break-table) {
			top : 48px;
		}
		@media screen and (min-width : $break-desktop) {

		}
		@media screen and (min-width : $break-desktop-fh) {
			top : 64px;
		}
	}
}


.header__logo {
	height : 12px;
	width  : 71px;
	@media screen and (min-width : $break-table) {
		height : 18px;
		width  : 107px;
	}
	@media screen and (min-width : $break-desktop-fh) {
		height : 24px;
		width  : 143px;
	}
}


.header__wrapper {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	padding         : 15px 15px 0 15px;

	@media screen and (min-width : $break-table) {
		align-items : flex-start;
		position    : relative;
	}

	@media screen and (min-width : $break-desktop) {

		&:before {
			background-image  : url("../images/icon/corners-left-top.svg");
			background-repeat : no-repeat;
			background-size   : 5px 4px;
			content           : "";
			height            : 4px;
			left              : 0;
			position          : absolute;
			top               : 0;
			width             : 5px;
			z-index           : 11;
		}

		&:after {
			background-image  : url("../images/icon/corners-right-top.svg");
			background-repeat : no-repeat;
			background-size   : 5px 4px;
			content           : "";
			height            : 4px;
			position          : absolute;
			right             : 0;
			top               : 0;
			width             : 5px;
			z-index           : 11;
		}
	}
}

.nav {
	display : block;
	padding : 48px 0 36px 0;

	@media screen and (min-width : $break-table) {
		padding : 0;
	}
}

.nav__list {

}

.nav__item {

	position   : relative;
	text-align : center;

	@media screen and (min-width : $break-table) {
		text-align : right;
	}

	&:not(:last-child) {
		margin-bottom : 16px;

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 10px;
		}

		@media screen and (min-width : $break-desktop-lg) {
			margin-bottom : 16px;
		}
	}

	a {
		color       : $neutral-gray;
		font-size   : 16px;
		font-weight : 400;
		line-height : 24px;
		transition  : color .3s ease;

		&:hover {
			color : rgba(221, 221, 221, 0.5);
		}
	}


	&.active {
		a {
			color : $pink;
		}

		&:after {
			background-color : $pink;
			content          : "";
			height           : 100%;
			position         : absolute;
			right            : -15px;
			top              : 0;
			width            : 1px;
			z-index          : 10;
		}
	}
}

.header__panel {
	background-color : #151515;
	height           : 101vh;
	padding          : 12px 12px;
	position         : absolute;
	right            : -150%;
	top              : 0;
	transition       : right .3s ease-in;
	width            : 100%;
	z-index          : 100;

	@media screen and (min-width : $break-table) {
		background-color : transparent;
		height           : auto;
		padding          : 0;
		position         : unset;
		right            : unset;
		width            : auto;
	}

	&.active {
		right : 0;
	}

	.line {
		@media screen and (min-width : $break-table) {
			display : none;
		}
	}

	.line-vertical {
		@media screen and (min-width : $break-table) {
			display : none;
		}
	}

	.line--bottom {
		bottom  : 18px;
		z-index : 28;

		@media screen and (min-width : $break-table) {
			display : none;
		}
	}

	&:before {
		background-image  : url("../images/icon/corners-left-top.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		content           : "";
		height            : 4px;
		left              : 12px;
		position          : absolute;
		top               : 12px;
		width             : 5px;
		z-index           : 11;

		@media screen and (min-width : $break-table) {
			display : none;
		}
	}

	&:after {
		background-image  : url("../images/icon/corners-right-top.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		content           : "";
		height            : 4px;
		position          : absolute;
		right             : 12px;
		top               : 12px;
		width             : 5px;
		z-index           : 11;

		@media screen and (min-width : $break-table) {
			display : none;
		}
	}
}

.burger-wrapper {
	align-items     : center;
	display         : flex;
	height          : 32px;
	justify-content : center;
	position        : relative;
	width           : 32px;
	z-index         : 100;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 1450px) {
		display : none;
	}
}

.header__coin {
	.coin-exchange:nth-child(n+5) {
		display : unset;
	}

	@media screen and (min-width : $break-table) {
		display : none;
	}
}

.header__wrap {
	display         : flex;
	flex-direction  : column;
	height          : 100%;
	justify-content : space-between;
	width           : 100%;
}

.header__menu {
	display        : flex;
	flex-direction : column;
	height         : 100%;
	overflow-x     : hidden;
	overflow-y     : auto;

	@media screen and (min-width : $break-table) {
		overflow : visible;
	}
}

.header__mob {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	padding         : 12px 12px;
	position        : relative;


	@media screen and (min-width : $break-table) {
		display : none;
	}

	.line--bottom {
		bottom : 0;
	}

	&:before {
		background-image  : url("../images/icon/corners-left-bottom.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		bottom            : 0;
		content           : "";
		height            : 4px;
		left              : 0;
		position          : absolute;
		width             : 5px;
		z-index           : 40;
	}

	&:after {
		background-image  : url("../images/icon/corners-right-bottom.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		bottom            : 0;
		content           : "";
		height            : 4px;
		position          : absolute;
		right             : 0;
		width             : 5px;
		z-index           : 40;
	}
}

.header__top {
	align-items     : center;
	display         : flex;
	justify-content : space-around;
	padding         : 24px 0;
	position        : relative;

	.line--top {
		top : 0;

		@media screen and (min-width : $break-table) {
			display : none;
		}
	}

	.line--bottom {
		bottom : 0;

		@media screen and (min-width : $break-table) {
			display : none;
		}
	}

	&:before {
		background-image  : url("../images/icon/corners-left-top.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		content           : "";
		height            : 4px;
		left              : 0;
		position          : absolute;
		top               : 0;
		width             : 5px;
		z-index           : 40;


	}

	&:after {
		background-image  : url("../images/icon/corners-right-top.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		content           : "";
		height            : 4px;
		position          : absolute;
		right             : 0;
		top               : 0;
		width             : 5px;
		z-index           : 40;
	}
}

.header__bottom {
	display         : flex;
	justify-content : space-around;
	padding         : 24px 0;
	position        : relative;

	&:before {
		background-image  : url("../images/icon/corners-left-bottom.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		bottom            : 5px;
		content           : "";
		height            : 4px;
		left              : 0;
		position          : absolute;
		width             : 5px;
		z-index           : 40;
	}

	&:after {
		background-image  : url("../images/icon/corners-right-bottom.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		bottom            : 5px;
		content           : "";
		height            : 4px;
		position          : absolute;
		right             : 0;
		width             : 5px;
		z-index           : 40;
	}
}

.burger {
	background-color : transparent;
	border           : none;
	cursor           : pointer;
	height           : 24px;
	padding          : 0;
	width            : 24px;

	@media screen and (min-width : $break-table) {
		display : none;
	}
}

.burger-close {
	background-color : transparent;
	border           : none;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 24px;
	padding          : 0;
	width            : 24px;
}


